.App {
  display: flex;
  flex-direction: column;
  width: 100vw;
  text-align: center;
  margin-bottom: 40px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  margin: 10px;
}

.App-header {
  font-family: 'MyWebFont';
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #333333;
}

.Header-Type {
  margin-top: 30px;
  font-size: 4rem;
  font-weight: 400;
  font-family: 'MyWebFont';
}

.Subtitle {
  margin-bottom: 30px;
  font-size: 1.25rem;
  font-weight: 400;
}

.App-link {
  color: #61dafb;
}

.Row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%
}
